import * as React from "react"
import Layout, {Content} from "./layout.js"
import "../styles/style.css"

export default function IndexPage() {
  return (
    <main>
      <Layout pageTitle="About Me">
        
        <Content contentTitle="Personal">
          Hi, I'm Simone! (she/they)
          <br/>
          I'm a creative person, and strive to practice as many different artistic mediums as I possibly can!
          <br/>
          My passions include playing piano, composing music, and sketching/painting.
          <br/>
          Ultimately, I hope to create something that others find meaningful :)
        </Content>
        
        <Content contentTitle="Professional">
          My background is in Mechatronics engineering. 
          <br/>
          I enjoy exploring and gathering knowledge on a variety of skills and topics, including 3D manufacturng, embedded systems programming, and robotics.
          <br/>
          As a software development professional, I'm currently focusing on frontend web development.
        </Content>

        <Content contentTitle="Site Info">
          This web page was built using Gatsby, React.js, and Tailwind CSS.
          <br/>
          Digital art was created in Krita and Sketchbook.
          <br/>
          3D Models were created in Blender, Fusion360, and Solidworks. 
          <br/>
          Animations were created in Krita and Blender.        
        </Content>

        <Content contentTitle="Contact">
            Send me an email or something won't ya --&gt; simone_hamby@proton.me
        </Content>
      </Layout>     
    </main>
  )
}

